<template>
    <div>
        <div class="view-box">
            <im :isShow="isShow"
                width="350px"
                height="650px"
                bottom="0"
                right="0"
                :userHandle="userHandle"
                :userGroupListHandle="userGroupListHandle"
                :chatMsgListHandle="chatMsgListHandle"
                :downClick="downClick"
                :webSocketUrl="webSocketUrl"></im>
        </div>
        <div class="btn-ok" @click="downClick" v-if="!isShow">
            <i class="icon"></i>
            <span class="text">在线沟通</span>
        </div>
    </div>
</template>

<script>
import Im from "../components/im/index";
import { userInfo, userGroupList, chatMsgList } from "../api/test";
export default {
    name: "home",

    data() {
        return {
            isShow: true,
            query: {
                userGroupListPage: 1,
                chatMsgListPage: 1
            },
            webSocketUrl: "ws://localhost:7272?t=ssss"
        };
    },
    components: {
        Im
    },
    methods: {
        downClick() {
            this.isShow = !this.isShow;
        },
        userGroupListHandle() {
            const _this = this;
            return new Promise(function(resolve, reject) {
                let data = {
                    type: "userGroup",
                    userGroupListPage: _this.query.userGroupListPage
                };
                userGroupList(data)
                    .then(response => {
                        _this.query.userGroupListPage += 1;
                        resolve(response.data);
                    })
                    .catch(() => {
                        reject(_this.groupList);
                    });
            });
        },
        chatMsgListHandle() {
            const _this = this;
            return new Promise(function(resolve, reject) {
                let data = {
                    type: "chatMsgList",
                    userGroupListPage: _this.query.chatMsgListPage
                };
                chatMsgList(data)
                    .then(response => {
                        _this.query.chatMsgListPage += 1;
                        resolve(response.data);
                    })
                    .catch(() => {
                        reject([]);
                    });
            });
        },
        userHandle() {
            return new Promise(function(resolve, reject) {
                let data = {
                    type: "userInfo",
                    userId: 1,
                    token: "xxxxx"
                };
                userInfo(data)
                    .then(response => {
                        resolve(response.data);
                    })
                    .catch(() => {
                        reject();
                    });
            });
        }
    },
    created() {}
};
</script>

<style type="text/scss" lang="scss">
.btn-ok {
    position: fixed;
    right: 30px;
    bottom: 30px;
    width: auto;
    padding: 20px 30px;
    cursor: pointer;
    border-radius: 33px;
    background-color: #6ed0ce;
    color: #ffffff;
    box-shadow: 7px 7px 12px 4px rgba(2, 25, 25, 0.05);
    text-align: center;
    .icon {
        vertical-align: middle;
        width: 20px;
        height: 20px;
        display: inline-block;
        margin-right: 10px;
        background-size: 100%;
        background-image: url("data:image/png;base64,");
    }
    .text {
        vertical-align: middle;
        line-height: 20px;
    }
}
.view-box {
    width: 100%;
    height: 100vh;
}
</style>
